@use "variables" as v;

@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin grid ( $colums, $gridGap ) {
    display: grid;
    grid-template-columns: repeat($colums, 1fr);
    gap: $gridGap;
}