@use "base/variables" as v;
@use "base/mixins" as m;

.content-aboutUs {

    @include m.tablet { 
        @include m.grid(2, 2rem)
    }

    .images {
        margin-bottom: 5rem;
        @include m.tablet { 
            margin: 0 2rem 0 0;
        }
        img {
            transform: rotate(-6deg);
            border: 3rem solid v.$white;
            box-shadow: 9px 10px 5px -7px rgb( 0 0 0 / .35);
        }
    }

    .text {
        blockquote {
            margin: 0;
            padding: 1rem;
            border-left: .5rem solid v.$primary_color;
            font-size: 2rem;
            font-weight: 700;
        }
    }
}